/* styles.css */

/* Ensure #screen and .game-grid fit within the viewport width */
#screen, .game-grid {
  width: 100%;
  max-width: 100vw;
  box-sizing: border-box; /* Ensure padding and border are included in the element's total width */
}

/* Center #gameText on all screen sizes */
#gameText {
  width: 45%;
  text-align: center;
  margin: 5px auto;
  display: flex;
  justify-content: center;
  align-items: center;
}

/* Styles for mobile devices, iPads, and tablets */
@media screen and (max-width: 1024px) {
  #gameText {
    width: 100%;
  }
}

/* Animation for fading out letters */
.fade-out {
  animation: fadeOut 0.5s forwards;
}

@keyframes fadeOut {
  to {
    opacity: 0;
  }
}

/* Green color for correct letters */
.correct-letter {
  color: green;
}

/* Red color for incorrect letters */
.incorrect-letter {
  color: red;
}
